<template>
  <div class="home" flexContainer>
    <div class="home-top">
      <div class="sele-city">
        常州发的
        <span></span>
      </div>
      <span>GT1养车</span>
      <div class="message">
      </div>
    </div>
    <div class="slider">
    </div>
    <div class="car-info">
      <div class="car-img">
        <img src="" alt="">
      </div>
      <div class="car-name">
        <div class="name">东风本田-思域</div>
        <div class="card-info">
          <div class="card">苏DB5A68</div>
          <div class="way">2300km</div>
        </div>
      </div>
      <div class="car-check" data-num="20">查看检测单</div>
    </div>
    <!-- <div class="gt1-menu">
      <ul>
        <li></li>
        <li></li>
        <li></li>
        <li></li>
        <li></li>
        <li></li>
      </ul>
    </div>
    <div class="gt1-server">

    </div> -->
  </div>
</template>

<script>
export default {
  name: 'home'
}
</script>

<style scoped lang="stylus" ref="stylesheet/stylus">
  @import "../../common/stylus/mixin.styl"
  .home
    flex-direction: column
    bg-image('../../common/imgs/home/homebg')
    background-size: 750px 540px
    background-repeat: no-repeat
    background-position:center top
    .home-top
      position: relative
      height: 80px
      .sele-city
        margin-top: 16px
        display: inline-block
        position: relative
        margin-left: 30px
        background-color: rgba(255, 255, 255, 0.85)
        border-radius: 5px
        font-size: 22px
        color: #fd9068
        padding: 16px 46px 16px 20px
        span
          position: absolute
          right: 0
          top: 0
          display: inline-block
          width: 32px
          height: 54px
          bg-image('../../common/imgs/home/sj-bottom')
          background-size: 12px 6px
          background-repeat: no-repeat
          background-position: left center
      & > span
        position: absolute
        left: 0
        right: 0
        top: 0
        bottom: 0
        margin: auto
        display: inline-block
        width: 180px
        line-height: 80px
        color: #fff
        font-size: 40px
        font-weight: 600
        transform: skewX(-15deg)
      .message
        float: right
        width: 80px
        height: 80px
        margin-right: 30px
        bg-image('../../common/imgs/mind/xx')
        background-size: 42px 42px
        background-repeat: no-repeat
        background-position: right center
    .slider
      height: 300px
    .car-info
      height: 187px
      width: 706px
      padding: 4px 7px 12px 7px
      display: flex
      margin: 0 auto
      bg-image('../../common/imgs/home/carbg')
      background-size: 706px 187px
      background-repeat: no-repeat
      background-position: center center
      .car-img
        width: 127px
        display: flex
        align-items: center
        justify-content: center
        img
          width: 50px
          height: 50px
          background-color: red
      .car-name
        flex: 1
        display: flex
        flex-direction: column
        align-items: flex-start
        justify-content: center
        .name
          height: 40px
          line-height: 40px
          font-size: 30px
          color: #5b5b5b
          font-weight: bolder
          transform: skewX(-10deg)
        .card-info
          padding-top: 7px
          height: 32px
          display: flex
          font-size: 24px
          color: #5b5b5b
          line-height: 34px
          .card
            width: 140px
            border-right: 1px solid #5b5b5b
          .way
            flex: 1
            padding-left: 28px
      .car-check
        position: relative
        width: 160px
        bg-image('../../common/imgs/home/jcd')
        background-size: 67px 65px
        background-repeat: no-repeat
        background-position: center 40px
        padding-top: 120px
        line-height: 30px
        color: #636363
        font-size: 26px
        text-align: center
        border-left: 1px solid #e7e7e7
        &:after
          content: attr(data-num)
          line-height: 20px
          text-align: center
          padding: 5px 10px
          font-size: 20px
          color: #fff
          border-radius: 24px
          background-color: red
          position: absolute
          top: 30px
          right: 20px
    .gt1-menu
      padding: 0px 8px
      ul
        display: flex
        flex-wrap: wrap
        li
          height: 232px
          width: 183.5px
          background-color: red
</style>



